<template>
  <div class="nav-bar"
       :class="headerFixed?'issFixed':''"
       ref="header">
    <div class="
       container">
      <div class="pro-title">
        {{title}}
      </div>
      <div class="pro-param">
        <a href=""></a><span>|</span>
        <a href=""></a><span>|</span>
        <a href=""></a><span>|</span>
        <slot name="buy"></slot>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "home",
  props: {
    title: {
      type: String,
      default: "oppo R8"
    },
  },
  data () {
    return {
      offsetTop: 0,
      offsetHeight: 0,
      headerFixed: 0
    }
  },
  mounted () {
    this.$nextTick(() => {
      // dom
      let header = this.$refs.header;
      // top
      this.offsetTop = header.offsetTop;
      // 
      this.offsetHeight = header.offsetHeight;
      // 
      window.addEventListener("scroll", this.handleScroll);
    });
  },
  unmounted () {
    //vue3destroyunmounted
    window.removeEventListener("scroll", this.handleScroll, false);
  },

  methods: {
    handleScroll () {
      // 
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // 
      this.headerFixed = scrollTop > this.offsetTop;
    }
  }
}
</script>


<style lang="scss">
@import "./../assets/scss/config.scss";
@import "./../assets/scss/mixin.scss";

.nav-bar {
  height: 70px;
  line-height: 70px;
  border-top: 1px solid $colorH;
  background-color: $colorG;
  z-index: 10;
  &.issFixed {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 4;
    box-shadow: 0 5px 5px $colorE;
  }

  .container {
    @include flex();
    .pro-title {
      font-size: $fontH;
      color: $colorB;
      font-weight: bold;
    }
    .pro-param {
      font-size: $fontJ;
      span {
        margin: 0 10px;
      }
      a {
        color: $colorC;
      }
    }
  }
}
</style>