<template>
  <div>
    <loading></loading>
    <form action=""
          v-html="conent">

    </form>

  </div>
</template>


<script>
import loading from '../components/loading.vue'
export default {
  name: "alipay",
  components: {
    loading
  },
  data () {
    return {
      conent: ""
    }
  },
  mounted () {
    this.paysubmit();
  },
  methods: {
    paysubmit () {
      this.$api.post("/pay", {
        orderId: this.$route.query.orderId,
        orderName: "Vue",//
        amount: 0.01, //
        payType: 1, //12
      }).then((res) => {
        this.conent = res.conent
        console.log("", res);
        setTimeout(() => {
          document.forms[0].submit();
        }, 100)
      })
    }
  }
}
</script>