<!--  -->
<template>
  <transition name="slide">
    <div class="modal"
         v-show="showModal">
      <div class="mask"></div>
      <div class="modal-dialog">
        <div class="modal-header">
          <span>{{title}}</span>
          <a href="javascript:;"
             class="icon-close"
             @click="$emit('close')"></a>
        </div>
        <div class="modal-body">
          <slot name="body"></slot>
        </div>
        <div class="modal-footer">
          <a href="javascript:;"
             class="btn"
             @click.prevent="$emit('submit','')"
             v-if="btnType==1">{{sureText}}</a>
          <a href="javascript:;"
             class="btn btn-default"
             v-if="btnType==2">{{cancelText}}</a>
          <div class="btn-group"
               v-if="btnType==3">
            <a href="javascript:;"
               class="btn"
               @click="$emit('certaintolist')">{{sureText}}</a>
            <a href="javascript:;"
               class="btn btn-default"
               @click="$emit('cancel')">{{cancelText}}</a>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  name: "modal",
  emits: ["close", "submit"],//vue3
  components: {},
  data () {
    //
    return {

    };
  },
  props: {
    // smallmiddlelargeform
    modalType: {
      type: String,
      default: "form"
    },
    // 
    title: String,
    // : 1: 2 3
    btnType: String,
    sureText: {
      type: String,
      default: ""
    },
    cancelText: {
      type: String,
      default: ""
    },
    showModal: Boolean,
  },
}
</script>
<style  lang="scss" scoped>
@import "../assets/scss/mixin.scss";
@import "../assets/scss/config.scss";
@import "../assets/scss/modal.scss";
</style>