        this.$store.dispatch("savecartcount", { cartnum: 0 })
      })
      console.log("logout");

    },
    goToCart () {
      this.$router.push('/cart')
      console.log("goToCart");
    },
    getProductList () {
      this.$api.get('mock/user/navheader.json')
        .then((res) => {
          // 
          this.phoneList = res.list
          console.log(this.phoneList);
        })
    }

